<template>
 <!-- :style="{ 'flex-direction' : isTeacher ? 'column' : 'column-reverse' }" -->
  <div class="video">
    <div class="view-item">
      <VideoIconBar
        class="fill"
        ref="remoteVideoPlayer"
        :iconShow="false"
        :disabled="!isMonitor"
        :upstage="!remoteUpstage"
        :stuInfo="stuInfo"
        :userName="isTeacher ? student.name : teacher.name"
        @interChange="interChange"
        @penChange="penChange"
        @micChange="micChange"
        @stageChange="remoteStageChange"
      >
        <remote-stream
          ref="remoteVideo"
          class="remoteVideo"
          :videotip="videotip"
          @stream="streamAddHandler"
        ></remote-stream>
      </VideoIconBar>
    </div>

    <div class="view-item">
      <VideoIconBar
        class="fill"
        ref="localVideoPlayer"
        :iconShow="false"
        :disabled="!isMonitor"
        :stuInfo="stuInfo"
        :userName="isTeacher ? teacher.name : student.name"
        :upstage="!localUpstage"
        @interChange="interChange"
        @penChange="penChange"
        @micChange="micChange"
        @stageChange="localStageChange"
      >
        <local-stream
          ref="localVideo"
          class="localVideo"
          :userType="userType"
          :device="device"
          :videotip="isTeacher ? 'no camera':'无摄像头'"
          :videoProfile="videoProfile"
          @complate="localVideoComplate"
          @error="localVideoError"
        ></local-stream>
      </VideoIconBar>
    </div>
  </div>
</template>

<script>
import LocalStream from "./LocalStream";
import RemoteStream from "./RemoteStream";
import VideoIconBar from './VideoIconBar.vue';
import { mapState, mapActions } from "vuex";
import Api from "@/service/index";
import Record from "@/service/Record";
import DomExchange from '@/utils/DomExchange.js'
import autoplay_src from '@/assets/autoplay/autoplay.mp3';

export default {
  components: {
    LocalStream,
    RemoteStream,
    VideoIconBar
  },
  props: {
    stuInfo: Object,
    classId: Number,
    appToken: String,
    token: Number,
    userType: Number,
  },
  data() {
    return {
      appid: null,
      device: null,
      remote_isStudent: false,
      exchange: new DomExchange(),
      remoteUpstage: false,
      localUpstage: false,
      hasFetchGroupChatRecord: false
    };
  },
  computed: {
    ...mapState("teacher", {
      teacher: state => state
    }),
    ...mapState("student", {
      student: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("room", {
      room: state => state
    }),
    videoProfile() {
      return '480P_1';
    },
    isStudent() {
      return this.userType == this.Global.UserType.STUDENT;
    },
    isTeacher() {
      return this.userType == this.Global.UserType.TEACHER;
    },
    isMonitor() {
      return this.userType == this.Global.UserType.ADMIN || this.userType == this.Global.UserType.PARENT;
    },
    videotip() {
      return this.isTeacher ? 'wait...' : '';
    },
    stageIndex() {
      return this.room.stageIndex;
    }
  },
  watch: {
    stageIndex(index) {
      this.restoreStage(index);
    }
  },
  methods: {
    ...mapActions("student", ["mutedStudent", "drawingStudent", "animateStudent"]),
    ...mapActions("room", ["updateStage"]),
    async init(appid, courseView) {
      const that = this;
      this.appid = appid;
      this.splash.progress(2, this.$t("splash_read_device"));
      let { isSuccess, code, message, result } = await this.Api.readDevice();
      this.device = isSuccess && result;
      this.$refs["localVideo"].init(this.token);
      // if(this.isTeacher){
      this.exchange.init(courseView, this.$refs['remoteVideoPlayer'].$el, this.$refs['localVideoPlayer'].$el);
      // }else {
      //   this.exchange.init(courseView, this.$refs['localVideoPlayer'].$el, this.$refs['remoteVideoPlayer'].$el);
      // }

      this.exchange.on('change', () => {
        that.course.completed = false;
      });

      this.restoreStage(this.stageIndex);
    },
    restoreStage(index) {
      if (this.exchange) {
        this.exchange.reset();
        this.remoteUpstage = this.localUpstage = false;
        if (index) {
          this.exchange.exchange(index, 0);
          if(index == 1){
            this.remoteUpstage = true;
          }else{
            this.localUpstage = true;
          }
        }
      }
    },
    localVideoComplate() {
      const that = this;
      var audio = document.createElement('audio');
      audio.src = autoplay_src;
      var promise = audio.play();
      audio.volume = 0.01;
      if (promise) {
        promise.then(function () {
          that.initVideoClient(that.classId, that.user.appToken, that.token);
        }).catch(err => {
          console.log("autoplay 策略检查:不可用", err.name, err.message);
          that.alert.show({
            text: "The browser needs your authorization to support the classroom.",
            label: "Authorize"
          }, cb => {
            that.initVideoClient(that.classId, that.user.appToken, that.token);
          });
        });
      }
    },
    localVideoError(err) {
      //TODO 处理本地视频流异常信息
      if (['PERMISSION_DENIED', 'PermissionDismissedError'].indexOf(err) != -1) {
        //提示用户许可访问媒体设备
        err = this.$t('brower_allow_tip');
      } else if (['MEDIA_OPTION_INVALID', 'NotReadableError'].indexOf(err) != -1) {
        //尽管用户同意使用设备,但还是启用异常
        err = this.$t('brower_notfound');
      } else if (['NotAllowedError'].indexOf(err) != -1) {
        //读取设备许可被禁用,提示
        err = this.$t('brower_allow_error');
      } else if (['NOT_SUPPORTED', 'MEDIA_OPTION_INVALID'].indexOf(err) != -1) {
        //浏览器版本过低或者不支持获取摄像头和麦克风
        err = this.$t('brower_updated');
      } else if ('DEVICES_NOT_FOUND' == err) {
        //设备没找到
        err = this.$t('video_device_no_text');
      } else {
        //其他异常
        err = this.$t('brower_undefined') + "</br>" + err;
      }

      this.alert.show({
        icon: "refresh",
        title: this.$t('brower_open_device'),
        text: err,
        label: this.$t("alert_error_refresh_label")
      }, () => {
        window.location.reload();
      });
    },
    videoClientError(err) {
      //TODO 处理通用error 信息
      if (err == 'SOCKET_DISCONNECTED') {
        console.error('视频服务已经断开');
      }
    },
    initVideoClient(classId, appToken, token) {
      const that = this;
      that.splash.progress(2, that.$t("splash_video_init"));
      let videoClient = this.videoClient;

      videoClient.on('rejected', function (token) {
        that.alert.show({
          icon: "exit",
          text: that.$t("alert_error_banned_text"),
          label: that.$t("alert_error_refresh_label")
        }, () => {
          window.location.reload();
        });
      });

      videoClient.on("error", that.videoClientError);
      videoClient.init(that.appid).then(() => {
        that.splash.progress(2, that.$t("splash_video_connect"));

        that.$refs["remoteVideo"].init(that.videoClient);

        videoClient.connect(classId, appToken, token).then((token, id, type) => {
          that.splash.progress(2, that.$t("splash_video_pub"));
          videoClient.publish(that.videoStream.localStream);
          that.splash.progress(2, that.$t("splash_video"));
          that.splash.complete(that.$t("splash_complated"));
        }).catch(function (err) {
          that.alert.show({
            icon: "refresh",
            text: JSON.stringify(err),
            label: that.$t("alert_error_refresh_label")
          }, () => {
            window.location.reload();
          });
        });
      }).catch(function (err) {
        that.alert.show({
          icon: "refresh",
          text: JSON.stringify(err),
          label: that.$t("alert_error_refresh_label")
        }, () => {
          window.location.reload();
        });
      });
    },
    streamAddHandler(id, type, token) {
      this.remote_isStudent = type == this.Global.UserType.STUDENT;
    },
    close() {
      this.videotip = "课程已经结束";
      this.closeVideoClient();
    },
    closeVideoClient() {
      if (this.videoClient) {
        this.videoClient.exit();
        this.videoClient = null;
      }
    },
    remoteStageChange(selected) {
      this.localUpstage = false;
      this.remoteUpstage = !selected;
      this.exchange.reset();
      if (this.remoteUpstage) {
        this.exchange.exchange(1, 0);
        this.updateStage(1);
        this.$socket.broadcast('stagechage', { index: 2});
      } else {
        this.updateStage(0);
        this.$socket.broadcast('stagechage', { index: 0 });
      }
    },
    localStageChange(selected) {
      this.remoteUpstage = false;
      this.localUpstage = !selected;
      this.exchange.reset();
      if (this.localUpstage) {
        this.exchange.exchange(2, 0);
        this.updateStage(2);
        this.$socket.broadcast('stagechage', { index: 1 });
      } else {
        this.updateStage(0);
        this.$socket.broadcast('stagechage', { index: 0 });
      }
    },
    resetStageChange() {
      this.remoteUpstage = false;
      this.localUpstage = false;
      this.exchange.reset();
      this.updateStage(0);
      this.$socket.broadcast('stagechage', { index: 0 });
    },
    interChange(selected) {
      this.animateChange(selected);
      selected && this.drawingChange(!selected);
    },
    animateChange(selected) {
      Record.save(Record.ANIMATE, { state: selected ? 1 : 2, extend: null }, this.student);
      this.animateStudent(selected);
      this.$socket.broadcast('animate', { id: this.student.id, enabled: selected });
    },
    penChange(selected) {
      this.drawingChange(selected);
      selected && this.animateChange(!selected);
    },
    drawingChange(selected) {
      Record.save(Record.DRAWING, { state: selected ? 1 : 2, extend: null }, this.student);
      this.drawingStudent(selected);
      this.$socket.broadcast('draw', { id: this.student.id, enabled: selected });
    },
    micChange(selected) {
      //true静音,false 解除静音
      Record.save(Record.MIC, { state: selected ? 2 : 1, extend: null }, this.student);
      this.mutedStudent(selected);
      this.$socket.broadcast('muted', { id: this.student.id, enabled: selected });
      this.videoClient && this.videoClient.toggleAudio(this.student.token, selected);
    },
    async showGroupChat(activeChat) {
      if (!this.hasFetchGroupChatRecord && this.user.type == activeChat) {
        let type, group_user_id;
        if (this.user.type == this.Global.UserType.TEACHER) {
          type = 1;
          group_user_id = this.teacher.id;
        } else {
          type = 2;
          group_user_id = this.student.id;
        }
        const { isSuccess, result } = await this.Api.getGroupChatRecord(type, group_user_id);
        if (isSuccess) {
          let list = [];
          result.list.forEach(item => {
            list.push({
              name: item.user_nickname,
              self: item.user_id == this.user.id,
              message: item.message
            })
          });
          this.$groupChat.init(list, this.user.name, '技术支持');
        }
        this.$groupChat.show();
      }
    },
    sendMessage(message) {
      let type;
      let group_user_id;
      if (this.user.type == this.Global.UserType.TEACHER) {
        type = 1; //管理员和老师私聊
        group_user_id = this.teacher.id;
      } else {
        type = 2; //管理员和学生私聊
        group_user_id = this.student.id;
      }

      this.$socket.broadcast('talk', {
        userId: this.user.id,
        userType: this.user.type,
        type: this.Global.UserType.ADMIN,
        info: message,
      // list: this.talkList,
      })
      this.Api.sendGroupChat(type, group_user_id, message);
    },
    listen() {
      if (this.user.type != this.Global.UserType.STUDENT
        && this.user.type != this.Global.UserType.TEACHER) return;

      this.$socket.on("share", (event, value, user) => {
        if (event == 'talk') {
          if (!this.$groupChat.shown) {
            this.showGroupChat(value.activeChat);
          }
          //发给自己的消息
          if (this.user.type == value.activeChat) {
            this.$groupChat.recive(value.info, user.name);
          }
        }
      });
      this.$groupChat.on('send', this.sendMessage)
    }
  },
  created() {
    this.listen();
  }
};
</script>

<style lang="less" scoped>
.video {
  width: 100%;
  display: flex;
  flex-flow: column;
  align-content: space-between;
  justify-content: space-between;
  overflow: hidden;
  .view-item {
    width: 444px;
    height: 334px;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    .fill {
      width: 100%;
      height: 100%;
    }
  }

  .localVideo,
  .remoteVideo {
    width: 100%;
    height: 100%;
  }
}
</style>

