<template>
  <div
    class="room-container"
    :style="{width:page.width+'px',height:page.height+'px',zoom:page.ratio}"
  >
    <Header @initRoom="initRoom" @onlineChange="onlineChange" :isTeacher="isTeacher"/>
    <div class="content">
      <template v-if="user.id">
        <video-admin
          v-if="isAdmin"
          class="left"
          ref="videoplay"
          :classId="room.roomId"
          :appToken="user.appToken"
          :token="user.token"
          :userType="user.type"
          :stuInfo="student"
        />

        <Video
          v-else
          class="left"
          ref="video"
          :classId="room.roomId"
          :appToken="user.appToken"
          :token="user.token"
          :userType="user.type"
          :stuInfo="student"
        />
      </template>

      <div class="right">
        <div class="course-area" :style="{width:paint.width+'px', height:paint.height+'px'}">
          <div ref="coursepanel" class="fill">
            <courseware class="fill" ref="course">
              <img slot="loading" class="fill" :src="bgMask">
            </courseware>
            <Painter
              type="local"
              :enable="paintEnable"
              :emitEnable="true"
              :receiveEnable="false"
              :width="paint.width"
              :height="paint.height"
              :ratio="page.ratio"
              :lineWidth="paint.lineWidth"
              :lineColor="paint.lineColor"
              @beginPaint="beginPaint"
              @painting="painting"
            />
            <Painter
              type="remote"
              :enable="false"
              :receiveEnable="true"
              :width="paint.width"
              :height="paint.height"
              :ratio="page.ratio"
            />
            <TextBox
              :enable="writeEnable"
              :width="paint.width"
              :height="paint.height"
              :ratio="page.ratio"
              :fontSize="paint.fontSize"
              :color="paint.lineColor"
              @beginWrite="beginWrite"
              @writing="writing"
            />

            <BaseButton class="screen stage-screen" icon="uu-upstage" @change="stageChange"/>
          </div>

          <div v-if="isAdmin" class="fill"></div>
          
          <EncourageAnimation class="encourage-animation"/>
          <Encourage
            class="encourage-container"
            v-show="paint.animateEnable"
            @encourage="encourage"
          />
          <bubble-window class="bubble-tip"></bubble-window>
          <socket-status></socket-status>
        </div>
        <Footer>
          <course-page-picker class="page-picker" slot="pagePicker" @change="pageChangeHandler"/>
        </Footer>
      </div>

      <UserOnlineList class="user-list" v-if="isAdmin" ref="userList" :list="onlineList"></UserOnlineList>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import Video from "./Video.vue";
import VideoAdmin from "./VideoAdmin.vue";
import BaseButton from './BaseButton.vue';
import Courseware from "@/components/Courseware.vue";
import CoursePagePicker from "@/components/CoursePagePicker.vue";
import BubbleWindow from "@/components/BubbleWindow.vue";
import SocketStatus from "@/components/SocketStatus.vue";
import UserOnlineList from "@/components/UserOnlineList.vue";
import Record from "@/service/Record";

import bg_cn from "@/assets/image/course_bg.png";
import bg_en from "@/assets/image/course_bg_en.png";

export default {
  components: {
    Header,
    Footer,
    Video,
    VideoAdmin,
    BaseButton,
    Courseware,
    CoursePagePicker,
    BubbleWindow,
    SocketStatus,
    UserOnlineList
  },
  data() {
    return {
      config: {
        appid: "c5f20309b7d64264aa73f1bc7e438de7"
      },
      onlineList: []
    }
  },
  computed: {
    ...mapState("page", {
      page: state => state
    }),
    ...mapState("room", {
      room: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("paint", {
      paint: state => state
    }),
    ...mapState("teacher", {
      teacher: state => state
    }),
    ...mapState("student", {
      student: state => state
    }),
    isTeacher() {
      return this.user.type == this.Global.UserType.TEACHER;
    },
    isAdmin() {
      return this.user.type == this.Global.UserType.ADMIN;
    },
    bgMask() {
      return this.isTeacher ? bg_en : bg_cn;
    },
    paintEnable() {
      return this.paint.activeAction == 'draw' && this.room.stageIndex === 0 && !this.isAdmin;
    },
    writeEnable() {
      return this.paint.activeAction == 'write' && this.room.stageIndex === 0 && !this.isAdmin;
    },
  },
  methods: {
    ...mapActions("page", ["setRatio"]),
    ...mapActions("room", ["endTheClass", "updateStage"]),
    ...mapActions("paint", ["endAnimate"]),
    ...mapActions("teacher", ["mergeTeacher"]),
    ...mapActions("student", ["mergeStudent", "mutedStudent", "drawingStudent", "animateStudent"]),
    resize() {
      const contentWidth = document.documentElement.clientWidth - this.page.left * 2; //新宽度
      const contentHeight = document.documentElement.clientHeight; //新高度
      let ratio = contentWidth / this.page.width;
      const height = this.page.height * ratio;
      if (height > contentHeight) {
        ratio = contentHeight / this.page.height;
      }
      this.setRatio(Number(ratio.toFixed(4)));
    },
    beginPaint(x, y) {
      this.$socket.broadcast('position', { type: "start", x, y, width: this.paint.lineWidth, color: this.paint.lineColor });
    },
    painting(x, y) {
      this.$socket.broadcast('position', { type: "move", x, y });
    },
    beginWrite(x, y) {
      this.$socket.broadcast('draw_text', { type: 'textstart', x, y, width: 300, height: 40 });
    },
    writing(x, y, width, height, fontSize, color, text) {
      this.$socket.broadcast('draw_text', { type: 'textchange', x, y, width, height, fontSize, color, text });
    },
    encourage(index, delay) {
      this.$socket.broadcast('excit', { index, delay })
      setTimeout(() => {
        this.endAnimate();
      }, delay)
    },
    pageChangeHandler(page, total) {
      this.$socket.broadcast("page", { page, total, reset: 0 });
      Record.save(Record.PAGE_CHANGE, { page: page });
    },
    getCourseAction(cb) {
      let that = this;
      this.Api.getCourseState().then(({ isSuccess, message, result }) => {
        if (isSuccess) {
          that.course.page = result.pageSetting || 1;
          let list = result.animationSetting.map(content => {
            return JSON.stringify(content);
          });
          that.course.actionlist = list;
        }
        cb && cb();
      });
    },
    async initRoom() {
      let page = 10;
      const that = this;
      this.videoClient.on('tokenExpire', function () {
        that.Api.getAgoraToken(1).then(({ isSuccess, code, message, result }) => {
          if (isSuccess) {
            that.videoClient.renewToken(result.token);
          } else {
            if ([10008].indexOf(code) != -1) {
              window.location.hash = "classover";

              that.alert.show({
                icon: "exit",
                text: that.$t('class_timeout_text'),
                title: that.$t("class_timeout_title"),
                label: that.$t("button_leave_label"),
              }, () => {
                if (that.isAdmin) {
                  window.location.hash = "classover";
                } else {
                  that.endTheClass().then(url => {
                    window.location.href = url || `/message?code=${code}&message=${message}`;
                  });
                }
              });
            }
          }
        });
      });

      //初始化视频模块
      if (this.isAdmin) {
        this.$nextTick(() => {
          this.$refs["videoplay"].init(this.config.appid, this.$refs['coursepanel']);
        });
      } else {
        this.$refs["video"].init(this.config.appid, this.$refs['coursepanel']);
      }
      this.$refs['course'].init();
    },
    async classOut() {
      let url = await this.endTheClass();
      if (url) {
        window.location.href = url;
      }
    },
    socketListener() {
      const that = this;
      if (!this.$socket) return;
      this.$socket.on("connected", () => {
        let { id, type, name, token, photo, uuid } = this.user;
        this.$socket.join({
          room_id: this.room.roomId,
          user_id: id,
          role: type,
          info: { name, token, photo, uuid }
        });
        //socket 连接成功
      });

      this.$socket.on("enterSuccess", () => {
        function resolve() {
          that.course.initPage(that.course.page);
          switch (that.user.type) {
            case that.Global.UserType.TEACHER:
              that.$socket.broadcast('stagechage', { index: that.room.stageIndex });
              that.$socket.broadcast('page', { page: that.course.page, total: that.course.total, reset: 1 });
              break;
            case that.Global.UserType.STUDENT:
              that.mergeStudent({ online: true });
              that.$socket.broadcast('students', that.student);
              break;
          }
        };

        if (that.$socket.isKoConnected) {
          that.getCourseAction(resolve);
        } else {
          resolve();
        }
      });

      this.$socket.on("userEnter", (id, userType, info) => {
        let us = { id: id, type: userType, name: info.name, photo: info.photo, uuid: info.uuid, online: true };
        if (this.user.type == this.Global.UserType.TEACHER) {
          this.$socket.broadcast('stagechage', { index: this.room.stageIndex });
          this.$socket.broadcast('page', { page: this.course.page, total: this.course.total, reset: 1 });
        }

        if (userType == this.Global.UserType.TEACHER) {
          this.mergeTeacher(us);
          this.$sensor.emit('registerPage', { anchorID: info.uuid });
        } else if (userType == this.Global.UserType.STUDENT) {
          this.mergeStudent(us);
        }

        this.onlineList.push({ uuid: info.uuid, id: id, type: userType, name: info.name, token: userType + "" + id });
      });

      this.$socket.on("userQuit", (id, userType, info) => {
        const that = this;
        if (userType == this.Global.UserType.TEACHER) {
          this.mergeTeacher({ online: false });
        } else if (userType == this.Global.UserType.STUDENT) {
          this.mergeStudent({ online: false });
        }

        this.onlineList.forEach((item, index) => {
          if (item.token == userType + "" + id) {
            that.onlineList.splice(index, 1);
          }
        });
      });

      this.$socket.on("share", (event, value, user) => {
        switch (event) {
          case "draw":
            this.drawingStudent(value.enabled); break;
          case "animate":
            if(this.isAdmin){
              this.course.hasAuthority(false);
            }else{
              this.course.hasAuthority(value.enabled);
            }
            
            this.animateStudent(value.enabled); 
            break;
          case "muted":
            if (this.user.type != this.Global.UserType.STUDENT) {
              this.videoClient && this.videoClient.toggleAudio(this.student.token, value.enabled);
            }
            this.mutedStudent(value.enabled); break;
          case "students":
            this.mergeStudent(value);
            break;
          case "stagechage":
            if (user.type == that.Global.UserType.TEACHER || user.type == that.Global.UserType.STUDENT) {
              if (user.type == that.Global.UserType.TEACHER && this.isAdmin) {
                value.index = value.index == 1 ? 2 : (value.index == 2 ? 1 : value.index);
              }
              that.updateStage(value.index);
            }
            break;
          case "refresh":
            if (value.type == this.user.type) {
              window.location.reload();
            }
            break;
          case "rostrum":
          case "flower":
            // this.emit(event, user, value);
            break;
          case "chat":
            this.$groupChat.emit("chat", user, value);
            break;
          case "excit": //激励动画
            this.$encourage.beginAnimation(value.index, value.delay)
            break;
          case "cls":
            this.$painter.clear();
            this.$textbox.clear();
            break;
          case "page":   // 课件页码更新
            if (value.reset) {
              this.getCourseAction(() => {
                this.course.page = value.page;
                this.course.completed && this.course.initPage(value.page);
              });
            } else {
              this.course.initPage(value.page);
            }
            break;
          case "action": // 课件动画指令
              this.course.mockAction(value);
            break;
          case "line":
            this.localDrawLine(value.points, value.color, value.width, user);
            break;
          case "position": {
            const { type, x, y, width, color } = value;
            if (type == "start") {
              this.$painter.beginPaint(x, y, width, color);
            } else if (type == "move") {
              this.$painter.painting(x, y);
            }
            break;
          }
          case "interaction": //互动开关
            this.interaction(value, user);
            break;
          case "draw_text": {
            const { type, x, y, width, height, fontSize, color, text } = value
            if (type === "textstart") {
              this.$textbox.beginWrite(x, y, width, height);
            } else if (type === "textchange") {
              this.$textbox.writing(x, y, width, height, fontSize, color, text);
            }
            break;
          }
          case "complete":
            this.alert.show({
              text: this.$t('class_is_over'),
              label: this.$t('button_leave_label')
            }, this.classOut);
            this.$refs['video'].close();
            this.$socket.close();
            break;
        }
      });

      this.$socket.on("enterReject", (code, info) => {
        if (code == 10040) {
          window.location.href = `/message?code=${code}&message=${info}`;
        } else {
          this.alert.show({
            icon: 'refresh',
            text: this.$t('alert_error_refresh_text'),
            label: this.$t('alert_error_refresh_label')
          }, () => {
            location.reload();
          });
        }
      });

      this.$socket.on("offline", () => {
        this.alert.show({
          icon: 'exit',
          text: this.$t('alert_offline_text'),
          label: this.$t('button_leave_label')
        }, this.classOut);
      });

      this.$socket.on("close", () => {
        const that = this;
        this.$socket.close();
        // 从当前socket继续查找到结束
        const item = this.$socket.nextSocketItem({ id: this.$socket.id });
        if (item) {
          setTimeout(() => {
            that.$socket.connect(item);
          }, 500);
        } else {
          this.alert.show({
            icon: 'unlink',
            text: this.$t('alert_connection_failed_text'),
            label: this.$t('button_leave_label'),
          }, this.classOut);
        }
      });
    },
    courseListener() {
      const that = this;
      //课件动作指令
      this.course.on(this.course.EVENT_ACTION, function (msg, page) {
        that.$socket.broadcast("action", msg);
        var data = JSON.parse(msg);
        data.page = page;
        Record.save(Record.ANIMATE_INFO, data);
      });

      this.course.on(this.course.EVENT_PAGE_COMPLETE, (page, total, single, action) => {
        this.$painter.clear();
        this.$textbox.clear();
      });
    },
    stageChange() {
      this.$refs['video'].resetStageChange();
    },
    onlineChange() {
      this.$refs['userList'].show();
    }
  },
  created() {
    this.resize();
    window.onresize = () => {
      this.resize();
    };
    this.socketListener();
    this.courseListener()
  }
}
</script>

<style lang="less">
@import url("../assets/style/variables");
.room-container {
  .content {
    display: flex;
    position: relative;
    justify-content: space-between;
    .left {
      width: 444px;
      height: 682px;
    }
    .right {
      width: 800px;
      height: 682px;
      .course-area {
        overflow: hidden;
        position: relative;
        border-radius: 11px;
        .fill {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }

        .bubble-tip {
          z-index: @zindex-bubble;
          position: absolute;
          bottom: 50px;
          left: 0;
        }

        .stage-screen {
          display: none;
        }
      }
      .encourage-animation {
        position: absolute;
        z-index: 5;
      }
      .encourage-container {
        position: absolute;
        bottom: 0;
        z-index: 4;
      }
      .page-picker {
        position: relative;
        margin-right: 20px;
        z-index: @zindex-footer+3;
        &:hover {
          background-color: @color-active;
        }
        .page-list-box {
          background: @color-active;
        }
      }
    }

    .user-list {
      position: absolute;
      right: 5%;
    }
  }

  .screen {
    z-index: 5;
    position: absolute;
    right: 15px;
    bottom: 15px;
  }
}
</style>

