<template>
  <div
    class="btn"
    :class="{interactive:disabled, selected: !selecteValue}"
    @click="btnClickHandler"
  >
    <icon-font
      class="icon-font"
      :icon="iconLabel"
    ></icon-font>
  </div>
</template>

<script>
export default {
  name: 'ToggleButton',
  props: {
    icon: {
      type: String,
      default: ""
    },
    selectIcon: {
      type: String
    },
    selected: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      selecteValue: this.selected
    }
  },
  watch: {
    selected(val) {
      this.selecteValue = val;
    }
  },
  computed: {
    iconLabel() {
      return this.selecteValue ? this.icon : this.selectIcon;
    }
  },
  methods: {
    btnClickHandler() {
      if (!this.disabled) {
        this.selecteValue = !this.selecteValue;
        this.$emit('change', this.selecteValue);
      }
    }
  }
}
</script>

<style lang="less" scoped>
.btn {
  width: 26px;
  height: 26px;
  background: rgba(62, 113, 224);
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  opacity: 0.65;
  &:hover {
    opacity: 1;
  }

  &.selected {
    opacity: 1;
    background: #d93730;
  }
  &.interactive {
    pointer-events: none;
  }
}
</style>

