import BaseEvent from './BaseEvent'
class DomExchange extends BaseEvent {
    //需要交换移动的dom
    parentNodes = [];
    nodes = [];
    constructor() {
        super();
    }

    init() {
        const arr = Array.prototype.slice.call(arguments) || [];
        arr.forEach((item, index) => {
            this.nodes.push(item);
            this.parentNodes.push(item.parentNode);
        });
    }

    /**
     * 恢复默认
     */
    reset() {
        this.parentNodes.forEach((element, index) => {
            element.insertBefore(this.nodes[index], element.firstChild);
            this.play(element);
        });
    }

    /**
     * 元素组件交换
     * @param {*} index 元素索引
     * @param {*} exindex 需要交换元素的索引
     */
    exchange(index, exindex) {
        let source = this.nodes[index];
        let target = this.nodes[exindex];
        let targetParentNode = this.parentNodes[exindex];
        if (source && target) {
            source.parentNode.insertBefore(target, source);
            targetParentNode.insertBefore(source, targetParentNode.firstChild);
        }
        this.play(source);
        this.play(target);
        this.emit('change', index, exindex);
    }

    play(element) {
        if (element) {
            const video = element.querySelector('video');
            video && video.play();
        }
        // const audio = element.querySelector('audio');
        // audio && audio.play();
    };
}
export default DomExchange;