<template>
  <div class="videodisplay">
    <!-- <span>这是视频描述信息</span> -->
    <span>{{videotip}}</span>
    <video
      id="localvideo"
      playsinline
      autoplay muted
    >浏览器不支持video播放</video>
  </div>
</template>
<script>
import icon_video from "@/assets/image/video_tip.png";
export default {
  name: "LocalStream",
  props: {
    userType: {
      type: Number,
      default: 1
    },
    device: {
      type: Object,
      default: null
    },
    videoProfile : {
      type: String,
      default: "480P_3"
    },
    videotip : {
      type: String,
      default: "暂无摄像头"
    }
  },
  computed: {
    icon() {
      return icon_video;
    },
    cameras() {
      return this.videoStream ? this.videoStream.cameras : [];
    },
    microphones() {
      return this.videoStream ? this.videoStream.microphones : [];
    },
    localStream() {
      return this.videoStream ? this.videoStream.localStream : null;
    }
  },
  methods: {
    init(token) {
      var that = this;
      this.splash.progress(2, that.$t("splash_open_device"));
      let videoStream = this.videoStream;
      videoStream.init().then(list => {
        that.splash.progress(2, that.$t("splash_init_local_video"));
        console.log(list);
        //组件功能 # 老师必须:摄像头和麦克风, 学生必须: 麦克风
        //麦克风操作
        if (that.microphones.length) {
          //摄像头操作
          if (that.userType == that.Global.UserType.TEACHER) {
            if (that.cameras.length) {
              that.createLocalVideo(token);
            } else {
              //老师没有摄像头操作提示
              that.alert.show({
                icon: 'refresh',
                title: that.$t('video_device_no_cam_title'),
                text: that.$t('video_device_no_cam_text'),
                label: this.$t("alert_error_refresh_label")
              }, () => {
                window.location.reload();
              });
            }
          } else {
            that.createLocalVideo(token);
          }
        } else { //没有麦克风
          if (that.cameras.length) {
            that.alert.show({
              icon: 'refresh',
              title: that.$t('video_device_no_mic_title'),
              text: that.$t('video_device_no_mic_text'),
              label: this.$t("alert_error_refresh_label")
            }, () => {
              window.location.reload();
            });
          } else {
            that.$emit("error", "DEVICES_NOT_FOUND");
          }
        }

      }).catch(info => {
        that.$emit("error", "DEVICES_NOT_FOUND");
      });
    },
    createLocalVideo(token) {
      const that = this;
      let videoStream = this.videoStream;
      if (this.device) {
        this.videoStream.cameraId = this.device.camera;
        this.videoStream.microphoneId = this.device.microphone;
      }
      videoStream.videoElement = document.querySelector("#localvideo");
      videoStream.videoProfile = this.videoProfile;

      console.log('视频发布规格:', videoStream.videoProfile, token);
      
      videoStream.streamID = token;
      videoStream.createStream().then(localStream => {
        that.splash.progress(2, that.$t("splash_create_local_video"));
        that.play();
        that.$emit("complate", localStream);
      }).catch(err => {
        that.$emit("error", err);
      });
    },
    play() {
      let videoStream = this.videoStream;
      videoStream.display();
    }
  }
};
</script>

<style lang="less" scoped>
.videodisplay {
  width: 100%;
  height: 100%;
  position: relative;
  background: rgba(0, 0, 0, 0.35) url("../assets/image/video_tip.png") no-repeat center;
  &.fill span {
    top: 60%;
  }
  span {
    position: absolute;
    width: 100%;
    text-align: center;
    display: block;
    top: 70%;
    color: #FFF;
  }
  .video-bg {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  video {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
  }
}
</style>

