<template>
  <div class="icon-bar">
    <slot></slot>
    <div
      class="tools"
      v-if="iconShow"
    >
      <toggle-button
        icon="uu-interview"
        selectIcon="uu-interview-disable"
        :disabled="!disabled"
        :selected="interSelected"
        @change="interChange"
      ></toggle-button>

      <toggle-button
        icon="uu-pen-enable"
        selectIcon="uu-pen-disable"
        :disabled="!disabled"
        :selected="penSelected"
        @change="penChange"
      ></toggle-button>

      <toggle-button
        icon="uu-mic-enable"
        selectIcon="uu-mic-disable"
        :disabled="!disabled"
        :selected="micSelected"
        @change="micChange"
      ></toggle-button>
    </div>

    <span
      class="user-name"
      v-if="!iconShow"
      v-show="userName"
    >{{userName}}</span>

    <toggle-button
      class="screen"
      icon="uu-upstage"
      v-if="disabled"
      :selected="upstage"
      selectIcon="uu-downstage"
      @change="stageChange"
    ></toggle-button>
  </div>
</template>

<script>
import ToggleButton from './ToggleButton.vue';
export default {
  name: 'VideoIconBar',
  components: {
    ToggleButton
  },
  props: {
    stuInfo: {
      type: Object,
      default: () => {}
    },
    userName: {
      type: String, default: ''
    },
    iconShow: {
      type: Boolean, default: false
    },
    disabled: {
      type: Boolean, default: false
    },
    upstage: {
      type: Boolean, default: false
    }
  },
  computed: {
    interSelected() {
      return this.stuInfo ? this.stuInfo.animate : false;
    },
    penSelected() {
      return this.stuInfo ? this.stuInfo.drawing : false;
    },
    micSelected() {
      return this.stuInfo ? !this.stuInfo.muted : false;
    }
  },
  methods: {
    interChange(selected) {
      this.$emit('interChange', selected);
    },
    penChange(selected) {
      this.$emit('penChange', selected);
    },
    micChange(selected) {
      this.$emit('micChange', !selected);
    },
    stageChange(selected) {
      this.$emit('stageChange', selected);
    }
  }
}
</script>

<style lang="less" scoped>
.icon-bar {
  .tools {
    position: absolute;
    width: 120px;
    height: 36px;
    left: 15px;
    bottom: 14px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    toggle-button {
      &:last-child {
        right: 50px;
      }
    }
  }

  .user-name {
    position: absolute;
    bottom: 0;
    color: #fff;
    background: rgba(0, 0, 0, 0.55);
    padding: 10px 30px 7px 20px;
    border-top-right-radius: 5px;
  }

  .screen {
    position: absolute;
    bottom: 14px;
    right: 15px;
  }
}
</style>

