<template>
  <div class="video">
    <div
      class="view-item"
      @mouseenter="studentEnter"
      @mouseleave="studentLeave"
    >
      <VideoIconBar
        class="fill"
        ref="videoPlayerStudent"
        :stuInfo="student"
        :userName="student.name"
        :iconShow="false"
      >
        <div
          :id="'videodisplay' + student.token"
          class="remoteVideo videodisplay"
          :userdata="JSON.stringify({id:student.id, type:student.type, 'extend_id': student.uuid})"
        >
        </div>
      </VideoIconBar>

      <div
        class="fill mask"
        v-if="student.online"
        v-show="studentMask"
      >
        <div class="btn-group">
          <button
            type="button"
            name="button"
            @click="refreshClient(Global.UserType.STUDENT)"
          >刷新</button>
          <button
            type="button"
            name="button"
            @click="showGroupChat(Global.UserType.STUDENT)"
          >私聊</button>
        </div>
      </div>
    </div>

    <div
      class="view-item"
      @mouseenter="teacherEnter"
      @mouseleave="teacherLeave"
    >
      <VideoIconBar
        class="fill"
        ref="videoPlayerTeacher"
        :userName="teacher.name"
        :iconShow="false"
      >
        <div
          :id="'videodisplay' + teacher.token"
          class="remoteVideo videodisplay"
          :userdata="JSON.stringify({id:teacher.id, type:teacher.type, 'extend_id': teacher.uuid})"
        >
        </div>
      </VideoIconBar>

      <div
        class="fill mask"
        v-if="teacher.online"
        v-show="teacherMask"
      >
        <div class="btn-group">
          <button
            type="button"
            name="button"
            @click="refreshClient(Global.UserType.TEACHER)"
          >刷新</button>
          <button
            type="button"
            name="button"
            @click="showGroupChat(Global.UserType.TEACHER)"
          >私聊</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RemoteStream from "./RemoteStream";
import VideoIconBar from './VideoIconBar.vue';
import { mapState, mapActions } from "vuex";

import Api from "@/service/index";
import Record from "@/service/Record";
import DomExchange from '@/utils/DomExchange.js'

export default {
  components: {
    RemoteStream,
    VideoIconBar
  },
  props: {
    stuInfo: Object,
    classId: Number,
    appToken: String,
    token: Number,
    userType: Number,
  },
  data() {
    return {
      appid: null,
      exchange: new DomExchange(),
      studentMask: false,
      teacherMask: false,
      activeChat: ''
    };
  },
  computed: {
    ...mapState("teacher", {
      teacher: state => state
    }),
    ...mapState("student", {
      student: state => state
    }),
    ...mapState("user", {
      user: state => state
    }),
    ...mapState("room", {
      room: state => state
    }),
    stageIndex() {
      return this.room.stageIndex;
    }
  },
  watch: {
    stageIndex(index) {
      this.restoreStage(index);
    }
  },
  methods: {
    ...mapActions("room", ["updateStage"]),
    init(appid, courseView) {
      const that = this;
      this.appid = appid;
      this.splash.progress(2, this.$t("splash_read_device"));
      this.initVideoClient(this.classId, this.appToken, this.token);

      this.exchange.on('change', () => {
        that.course.completed = false;
      });

      this.exchange.init(courseView, this.$refs['videoPlayerStudent'].$el, this.$refs['videoPlayerTeacher'].$el);
      this.restoreStage(this.stageIndex);
    },
    restoreStage(index) {
      if (this.exchange) {
        this.exchange.reset();
        if (index) {
          this.exchange.exchange(index, 0);
        }
      }
    },
    videoClientError(err) {
      //TODO 处理通用error 信息
      if (err == 'SOCKET_DISCONNECTED') {
        console.error('视频服务已经断开');
      }
    },
    initVideoClient(classId, appToken, token) {
      const that = this;
      that.splash.progress(2, that.$t("splash_video_init"));
      let videoClient = this.videoClient;
      videoClient.on("error", that.videoClientError);
      videoClient.init(that.appid).then(() => {
        that.splash.progress(2, that.$t("splash_video_sub"));

        videoClient.on("stream", function (id, type, token) {
          console.log("STREAM ADD :::", id, type, token);
          videoClient.player(token, document.querySelector('#videodisplay' + token));
        });

        videoClient.on("removed", function (id, type, token) {
          console.log("STREAM REMOVED :::", id, type, token);
          videoClient.stopPlayer(token);
        });

        videoClient.connect(classId, appToken, token).then((token, id, type) => {
          that.splash.progress(2, that.$t("splash_video"));
          that.splash.complete(that.$t("splash_complated"));
          console.log('进入成功');
        }).catch(function (err) {
          that.alert.show({
            icon: "refresh",
            text: JSON.stringify(err),
            label: that.$t("alert_error_refresh_label")
          }, () => {
            window.location.reload();
          });
        });
      }).catch(function (err) {
        that.alert.show({
          icon: "refresh",
          text: JSON.stringify(err),
          label: that.$t("alert_error_refresh_label")
        }, () => {
          window.location.reload();
        });
      });
    },
    close() {
      this.videotip = "课程已经结束";
      this.closeVideoClient();
    },
    closeVideoClient() {
      if (this.videoClient) {
        this.videoClient.exit();
        this.videoClient = null;
      }
    },
    studentEnter() {
      this.studentMask = true;
    },
    studentLeave() {
      this.studentMask = false;
    },
    teacherEnter() {
      this.teacherMask = true;
    },
    teacherLeave() {
      this.teacherMask = false;
    },
    refreshClient(type) {
      this.alert.show({
        icon: "refresh",
        text: "确认要刷新" + (type == this.Global.UserType.TEACHER ? "老师" : "学生") + "上课教室, 并中断当前课程吗?",
        label: "刷新",
        cancellabel: this.$t("button_cancel_label")
      }, () => {
        this.$socket.broadcast('refresh', { type: type });
      });
    },
    async showGroupChat(chatWith) {
      if (chatWith != this.activeChat) {
        let type, group_user_id, otherName;
        if (chatWith == this.Global.UserType.TEACHER) {
          type = 1;
          group_user_id = this.teacher.id;
          otherName = this.teacher.name;
        } else {
          type = 2;
          group_user_id = this.student.id;
          otherName = this.student.name;
        }
        const { isSuccess, result } = await this.Api.getGroupChatRecord(type, group_user_id);
        if (isSuccess) {
          let list = [];
          result.list.forEach(item => {
            list.push({
              name: item.user_nickname,
              self: item.user_id == this.user.id,
              message: item.message
            })
          });
          this.$groupChat.init(list, this.user.name, otherName);
        }
      }
      this.$groupChat.show();
      this.activeChat = chatWith;
    },
    async sendMessage(message) {
      let type, group_user_id;
      if (this.activeChat == this.Global.UserType.TEACHER) {
        type = 1;
        group_user_id = this.teacher.id;
      } else {
        type = 2;
        group_user_id = this.student.id;
      }
      const { isSuccess } = await this.Api.sendGroupChat(type, group_user_id, message);
      if (isSuccess) {
        this.$socket.broadcast('talk', {
          userId: this.user.id,
          userType: this.user.type,
          activeChat: this.activeChat,
          type: this.Global.UserType.ADMIN,
          info: message,
          // list: this.talkList,
        })
      }
    },
    listen() {
      if (this.user.type != this.Global.UserType.ADMIN) return;
      this.$socket.on("share", (event, value, user) => {
        if (event == 'talk') {
          if (!this.$groupChat.shown) {
            this.showGroupChat(user.type);
          }
          this.$groupChat.recive(value.info, user.name)
        }
      });
      this.$groupChat.on('send', this.sendMessage)
    }
  },
  created() {
    this.listen();
  }
};
</script>

