import * as types from './mutation-types'

export default {
    namespaced: true,
    state: {
        width: 1260,
        height: 757,
        left: 43,
        ratio: 1,
    },
    mutations: {
        [types.SETRATIO](state, ratio) {
            state.ratio = ratio;
        },
    },
    actions: {
        setRatio({
            commit
        }, ratio) {
            commit(types.SETRATIO, ratio)
        }
    },
    getters: {}
}